.resume-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: fit-content;
    padding: 160px 0 0 0;
}

.resume-container .highlighted-btn {
    margin: -48px 0 0 0;
}

.resume-container .highlighted-btn:hover {
    background-color: #1f2235;
    color: white;
    font-size: 13px;
}

.resume-card {
    display: flex;
    height: 360px;
    width: 1000px;
}

.resume-bullets {
    height: 100%;
    box-shadow: 15px 0 9px -15px #1f2235;
    width: 320px;
}

.resume-bullet-details {
    flex-grow: 1;
    width: 600px;
    overflow: hidden;
    padding: 0 0 0 70px;
}

.experience-description {
    margin: 10px 0 0 0;
    text-align: justify;
    max-width: 100%;
}

.resume-details-carousel {
    transition: transform 1s ease-out;
}

.bullet-container {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
}

.bullets {
    width: 80%;
    position: relative;
    z-index: 2;
}

.bullet {
    display: flex;
    align-items: center;
    background-color: #1f2235;
    height: 40px;
    margin: 15px 0;
    padding: 0 8px;
    border: 20px;
    width: 30px;
    transition: width .6s ease;
    cursor: pointer;
}

.bullet-label {
    font-size: 14px;
    white-space: nowrap;
    font-family: "Poppins SemiBold";
}

.bullet-logo {
    height: 16px;
    margin: 0 30px 0 0;
}

.bullet-icons {
    width: 36px;
    height: 100%;
    z-index: 1;
    background-color: #1f2235;
    position: absolute;
}

.selected-bullet {
    color: white;
    width: 100%;
}

.resume-screen-container {
    display: flex;
    flex-direction: column;
    height: 360px;
    width: 100%;
}

.resume-heading {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    font-family: "Poppins Bold";
    justify-content: center;
}

.resume-main-heading {
    position: relative;
    display: flex;
    margin-top: 32px;
    justify-content: space-between;
}

.heading-bullet {
    display: flex;
    position: absolute;
    left: -16px;
    height: 40px;
    width: 8px;
    top: 5px;
    background-color: var(--dark-orange);
}

.resume-sub-heading {
    font-size: 14px;
    font-family: "Poppins Regular";
    color: #1f2235;
}

.resume-heading-description {
    font-size: 12px;
    text-align: justify;
}

.heading-date {
    background-color: var(--dark-orange);
    padding: 4px 12px;
    font-size: 14px;
    border-radius: 14px;
    color: white;
}

.skill-parent {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 50%;
}

.programming-skill-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.skill-percentage {
    position: relative;
    height: 15px;
    width: 70%;
    background-color: var(--dark-orange);
}

.skill-percentage .active-percentage-bar {
    height: 15px;
    transition: .8s ease;
    position: absolute;
    background-color: #1f2235;
}

.skill-parent span {
    font-family: "Poppins SemiBold";
    font-size: 16px;
    color: var(--dark-orange);
}

@media only screen and (max-width:1110px) {
    .resume-card {
        width: 880px;
    }

    .resume-bullet-details {
        padding: 0 0 0 70px;
    }
}

@media only screen and (max-width: 1023px) {
    .resume-content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .bullets {
        width: 90%;
    }

    .resume-card {
        width: 90%;
        height: fit-content;
        display: flex;
        flex-direction: column;
        margin: 30px 0;
        align-items: center;
    }

    .resume-bullet-details {
        height: 360px;
        width: fit-content;
        padding: 0;
    }

    .resume-bullets {
        width: 100%;
        margin: 30px 0;
    }

    .selected-bullet {
        width: 100%;
    }
}